<template>
  <div class="about" v-if="isAuthenticated">
    <h1>This is an about page</h1>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  computed: {
    ...mapGetters(['isAuthenticated'])
  },
  watch: {
    isAuthenticated(newVal) {
      if (!newVal) {
        this.$router.push('/login')
      }
    }
  },
  mounted() {
    if (!this.isAuthenticated) {
      this.$router.push('/login')
    }
  }
}
</script>

<style scoped>
.about {
  padding: 20px;
}
</style>
