import { createStore } from 'vuex'
import axios from 'axios'

export default createStore({
  state: {
    // 초기 상태로, localStorage에서 사용자 이름과 토큰을 가져와 설정
    username: localStorage.getItem('username') || '',
    token: localStorage.getItem('token') || ''
  },
  getters: {
    // 사용자가 인증되었는지 여부를 반환 (토큰이 있으면 true, 없으면 false)
    isAuthenticated: state => !!state.token,
    // 현재 로그인한 사용자의 사용자 이름을 반환
    username: state => state.username
  },
  mutations: {
    // 사용자 이름과 토큰을 state에 설정하고, localStorage에도 저장
    setAuth(state, { username, token }) {
      state.username = username
      state.token = token
      localStorage.setItem('username', username)
      localStorage.setItem('token', token)
    },
    // 사용자 이름과 토큰을 state에서 제거하고, localStorage에서도 삭제
    clearAuth(state) {
      state.username = ''
      state.token = ''
      localStorage.removeItem('username')
      localStorage.removeItem('token')
    }
  },
  actions: {
    // 사용자 이름과 비밀번호를 사용하여 로그인을 시도
    login({ commit }, { username, password }) {
      return axios.post('/jwtdemo/api/auth/login', { username, password })
        .then(response => {
          const token = response.data.accessToken // Postman 결과와 동일하게 맞춤
          commit('setAuth', { username, token })
        })
        .catch(error => {
          throw new Error('Invalid credentials')
        })
    },
    // 로그아웃을 수행하여 state를 초기화
    logout({ commit }) {
      return axios.get('/jwtdemo/api/auth/logout')
        .then(() => {
          commit('clearAuth')
        })
    }
  }
})
